/* write your css here */
@tailwind base; 
@tailwind components; 
@tailwind utilities;

html , body{
   /* overflow:  hidden; */
   height: 100%;
   width: 100%;
}
.section-1{
    border:2px solid red;
}
.border{
    border: 0px  solid silver ;
}
.form-style {
    @apply rounded-lg bg-richblack-700 p-3 text-[16px] leading-[24px] text-richblack-5 shadow-[0_1px_0_0] shadow-white/50 placeholder:text-richblack-400 focus:outline-none;
  }
  .lable-style {
    @apply text-[14px] text-richblack-5;
    @apply font-extrabold capitalize;
    
  }
  td{
    /* border: 2px solid rgb(202, 202, 202); */
    width: 22%;
    text-transform: capitalize;
  }
  td img{
    margin: auto;
  }
  tr{
    margin-bottom: 50px;
  }
  